.playlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--transparent-background);
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 0 5px var(--transparent-background);
  border-radius: 5px;

  input {
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: var(--border-bottom);
    font-family: "Poppins", sans-serif;
    font-size: 1.55rem;
    color: var(--text-color);
  }
}
