.track {
  display: flex;
  align-items: center;
  border-bottom: var(--border-bottom);

  padding: 1rem 0;

  h3 {
    margin-bottom: 0.4rem;
  }

  .trackAction {
    background: transparent;
    color: var(--text-color-op8);
    font-size: 1.2rem;

    padding: 0.5rem;
    font-size: 1.05rem;
    transition: all 0.25s;

    &:hover {
      font-size: 1.3rem;
    }
  }
}
